.header { 
    width: 100%; 
    height: 5rem; 
    display: flex; 
    align-items: center; 
    justify-content: space-between; 
    background-color: white; 
    padding: 0 10%; 
} 
    
.logo { 
    font-size: 1.4rem; 
    color: white; 
    font-weight: bold; 
} 
    
.header ul { 
    list-style: none; 
    margin: 0; 
    padding: 0; 
    display: flex; 
    align-items: baseline; 
} 
.header li { 
    margin-left: 2rem; 
} 
        
.header a { 
    text-decoration: none; 
    font-size: 1.2rem; 
    color: black; 
} 
        
    .header a:hover, 
    .header a:active, 
    .header a.active { 
    color: orange; 
    } 
        
       